import Vue from 'vue'
import App from './App.vue'
import i18n from './i18n'
import VueGtm from '@gtm-support/vue2-gtm'

Vue.config.productionTip = false
Vue.use(VueGtm, {
  id: 'GTM-NX9MJ7M',
  defer: true,
  compatibility: true
})


new Vue({
  render: h => h(App),
  i18n
}).$mount('#app')
